<template>
  <div class="js_wrapper" v-loading="loading">
    <collapse v-bind:data="htmlInfors" v-on:handleChange="handleChange"></collapse>
  </div>
</template>
<script>
import { ERR_OK } from "@/api/config.js";
import { mapGetters } from "vuex";
import collapse from "@/base/Collapse";

export default {
  data() {
    return {
      htmlInfors: null,
      activeEl: 0
    };
  },
  created() {
    this._getHtmlInfor();
  },
  computed: {
    ...mapGetters(["loading"])
  },
  methods: {
    handleChange(val) {
      console.log(val);
    },
    _getHtmlInfor() {
      this.$http(
        "get",
        "/html"
      )
      .then(res => {
        if (res.status == ERR_OK) {
          this.htmlInfors = res.data.data;
        }
      })
      .catch(err => {
        console.log(err.message);
      });
    }
  },
  components: {
    collapse
  }
};
</script>
<style scoped>
.js_wrapper {
  width: 100%;
  height: 100%;
}
</style>


