<template>
  <div class="hello">
   <el-container>
      <el-header>Header</el-header>
      <el-container>
        <el-aside width="20%">
          <el-tree
            :data="data"
            :props="defaultProps"
            accordion
            @node-click="handleNodeClick">
          </el-tree>
        </el-aside>
        <el-container>
          <el-main>
            <keep-alive>
              <router-view></router-view>
            </keep-alive>
          </el-main>
          <el-footer>Footer</el-footer>
        </el-container>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      data: [
        {
          label: "HTML",
          router: "HTML"
        },
        {
          label: "Css",
          router: "Css"
        },
        {
          label: "JS",
          router: "JS",
          children: [
            {
              label: "原生JS",
              router: "JS"
            },
            {
              label: "ES6",
              router: "ES6"
            }
          ]
        },
        {
          label: "框架库",
          router: "Frame",
          children: [
            {
              label: "React",
              router: "React"
            },
            {
              label: "Vue",
              router: "VueIndex"
            }
          ]
        }
      ],
      defaultProps: {
        children: "children",
        label: "label"
      }
    };
  },
  methods: {
    handleNodeClick(data) {
      console.log(data);
      const { router } = data;
      // if (document.getElementById("loadingStyle")) {
      //   var loadingstyle = document.getElementById("loadingStyle");
      //   loadingstyle.style.background = "red"
      //   loadingstyle.style.left = "50%"
      //   loadingstyle.style.fontSize = '100px' 
      //   loadingstyle.style.color = "#fff"
      //   loadingstyle.innerHTML = `${router}`
      // }
      this.$router.push({
        path: `/${router}`
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello {
  height: 100%;
}
.el-container {
  height: 100%;
}
.el-header,
.el-footer {
  background-color: #b3c0d1;
  color: #333;
  text-align: center;
  line-height: 60px;
}

.el-aside {
  background-color: #d3dce6;
  color: #333;
  text-align: center;
  min-width: 90px;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  text-align: center;
  width: 100%;
  height: 100%;
}
</style>


