<template>
  <div class="js_wrapper" v-loading="loading">
    <collapse v-bind:data="jsInfors" v-on:handleChange="handleChange"></collapse>
  </div>
</template>
<script>
import { ERR_OK } from "@/api/config.js";
import { mapGetters } from "vuex";
import collapse from "@/base/Collapse";

export default {
  data() {
    return {
      jsInfors: null,
      activeEl: 0
    };
  },
  created() {
    this._getJsInfor();
  },
  computed: {
    ...mapGetters(["loading"])
  },
  methods: {
    handleChange(val) {
      console.log(val);
    },
    _getJsInfor() {
      this.$http("get", "/js")
        .then(res => {
          if (res.status == ERR_OK) {
            this.jsInfors = res.data.data;
          }
        })
        .catch(err => {
          console.log(err.message);
        });
    }
  },
  components: {
    collapse
  }
};
</script>
<style scoped>
.js_wrapper {
  width: 100%;
  height: 100%;
}
</style>


