<template>
    <el-collapse v-model="activeNames" @change="handleChange">
        <el-collapse-item v-for="item in data"  v-bind:key="item.id" v-bind:title="item.question" v-bind:name="item.id">
            <div class="content" v-html="item.answer"></div>
        </el-collapse-item>
    </el-collapse>
</template>
<script>
export default {
  data() {
    return {
      activeNames: [0]
    };
  },
  props: {
    data: {
      type: Array,
      default: null
    }
  },
  methods: {
    handleChange(val) {
      this.$emit("handleChange", val);
    }
  }
};
</script>
<style scoped>
.content {
  text-align: left;
  padding-left: 20px;
  padding-right: 20px;
  line-height: 25px;
}
</style>


