import Vue from 'vue'
import Router from 'vue-router'
import FrameWork from '@/components/FrameWork'
import Html from '@/components/Html/Html'

import JSIndex from '@/components/JS/index'
import JS from '@/components/JS/components/JS'
import ES6 from '@/components/JS/components/ES6'

import Css from '@/components/Css/Css'

import FrameIndex from '@/components/Frame/index'
import React from '@/components/Frame/components/React'
import VueIndex from '@/components/Frame/components/VueIndex'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [{
    path: '/',
    name: 'FrameWork',
    component: FrameWork,
    children: [{
        path: '/Html',
        name: 'Html',
        component: Html
      },
      {
        path: '/JS',
        name: 'JSIndex',
        component: JSIndex,
        children: [{
            path: '/JS',
            name: 'JS',
            component: JS,
          },
          {
            path: '/ES6',
            name: 'ES6',
            component: ES6,
          }
        ]
      },
      {
        path: '/Css',
        name: 'Css',
        component: Css
      },
      {
        path: '/Frame',
        name: 'FrameIndex',
        component: FrameIndex,
        children: [{
            path: '/React',
            name: 'React',
            component: React,
          },
          {
            path: '/VueIndex',
            name: 'VueIndex',
            component: VueIndex,
          }
        ]
      }
    ]
  }]
})



